
.deck-card {
  position: relative;
  margin: 4px;
}
.deck-card img {
  margin-bottom: 0;
}
.deck-card-text {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  padding: 6px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #e9ecef;
  opacity: 0;
  transition: opacity 0.15s;
  text-align: center;
  color: #343a40;
  word-wrap: break-word;
  line-height: 1.125em;
  font-size: 0.85em;
}
.deck-card-text:focus, .deck-card-text:hover {
    opacity: 1;
}
