
.deck-content {
  padding: 4px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.deck-part {
  margin-bottom: 1.25em;
}
.deck-part h3 {
    font-size: 1rem;
    display: initial;
}
.deck-part-main .deck-content {
    background-color: #9f7151;
}
.deck-part-extra .deck-content {
    background-color: #9683b8;
}
.deck-part-side .deck-content {
    background-color: #799a60;
}
