
.decktool {
  /* HTML*/
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  /* Body */
  margin: 0;
  font-size: 1rem;
  font-weight: 400;
  color: #212529;
  text-align: left;
  background-color: #fff;
}
.decktool *,
  .decktool *::before,
  .decktool *::after {
    box-sizing: border-box;
}
.decktool article,
  .decktool aside,
  .decktool dialog,
  .decktool figcaption,
  .decktool figure,
  .decktool footer,
  .decktool header,
  .decktool hgroup,
  .decktool main,
  .decktool nav,
  .decktool section {
    display: block;
}
.decktool [tabindex="-1"]:focus {
    outline: 0 !important;
}
.decktool hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
}
.decktool h1,
  .decktool h2,
  .decktool h3,
  .decktool h4,
  .decktool h5,
  .decktool h6 {
    margin-top: 0;
    margin-bottom: 0.5rem;
}
.decktool p {
    margin-top: 0;
    margin-bottom: 1rem;
}
.decktool abbr[title],
  .decktool abbr[data-original-title] {
    text-decoration: underline;
    text-decoration: underline dotted;
    cursor: help;
    border-bottom: 0;
}
.decktool address {
    margin-bottom: 1rem;
    font-style: normal;
    line-height: inherit;
}
.decktool ol,
  .decktool ul,
  .decktool dl {
    margin-top: 0;
    margin-bottom: 1rem;
}
.decktool ol ol,
  .decktool ul ul,
  .decktool ol ul,
  .decktool ul ol {
    margin-bottom: 0;
}
.decktool dt {
    font-weight: 700;
}
.decktool dd {
    margin-bottom: 0.5rem;
    margin-left: 0;
}
.decktool blockquote {
    margin: 0 0 1rem;
}
.decktool dfn {
    font-style: italic;
}
.decktool b,
  .decktool strong {
    font-weight: bolder;
}
.decktool small {
    font-size: 80%;
}
.decktool sub,
  .decktool sup {
    position: relative;
    font-size: 75%;
    line-height: 0;
    vertical-align: baseline;
}
.decktool sub {
    bottom: -0.25em;
}
.decktool sup {
    top: -0.5em;
}
.decktool a {
    color: #007bff;
    text-decoration: none;
    background-color: transparent;
    -webkit-text-decoration-skip: objects;
}
.decktool a:hover {
      color: #0056b3;
      text-decoration: underline;
}
.decktool a:not([href]):not([tabindex]) {
    color: inherit;
    text-decoration: none;
}
.decktool a:not([href]):not([tabindex]):hover, .decktool a:not([href]):not([tabindex]):focus {
      color: inherit;
      text-decoration: none;
}
.decktool a:not([href]):not([tabindex]):focus {
      outline: 0;
}
.decktool pre,
  .decktool code,
  .decktool kbd,
  .decktool samp {
    font-family: monospace;
    font-size: 1em;
}
.decktool pre {
    margin-top: 0;
    margin-bottom: 1rem;
    overflow: auto;
    -ms-overflow-style: scrollbar;
}
.decktool figure {
    margin: 0 0 1rem;
}
.decktool img {
    vertical-align: middle;
    border-style: none;
}
.decktool svg:not(:root) {
    overflow: hidden;
}
.decktool table {
    border-collapse: collapse;
}
.decktool caption {
    padding-top: 0.75rem;
    padding-bottom: 0.75rem;
    color: #6c757d;
    text-align: left;
    caption-side: bottom;
}
.decktool th {
    text-align: inherit;
}
.decktool label {
    display: inline-block;
    margin-bottom: 0.5rem;
}
.decktool button {
    border-radius: 0;
}
.decktool input,
  .decktool button,
  .decktool select,
  .decktool optgroup,
  .decktool textarea {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}
.decktool button,
  .decktool input {
    overflow: visible;
}
.decktool button,
  .decktool select {
    text-transform: none;
}
.decktool button,
  .decktool html [type="button"],
  .decktool [type="reset"],
  .decktool [type="submit"] {
    -webkit-appearance: button;
}
.decktool button::-moz-focus-inner,
  .decktool [type="button"]::-moz-focus-inner,
  .decktool [type="reset"]::-moz-focus-inner,
  .decktool [type="submit"]::-moz-focus-inner {
    padding: 0;
    border-style: none;
}
.decktool button:focus {
    outline: 1px dotted;
    outline: 5px auto -webkit-focus-ring-color;
}
.decktool input[type="radio"],
  .decktool input[type="checkbox"] {
    box-sizing: border-box;
    padding: 0;
}
.decktool input[type="date"],
  .decktool input[type="time"],
  .decktool input[type="datetime-local"],
  .decktool input[type="month"] {
    -webkit-appearance: listbox;
}
.decktool textarea {
    overflow: auto;
    resize: vertical;
}
.decktool fieldset {
    min-width: 0;
    padding: 0;
    margin: 0;
    border: 0;
}
.decktool legend {
    display: block;
    width: 100%;
    max-width: 100%;
    padding: 0;
    margin-bottom: 0.5rem;
    font-size: 1.5rem;
    line-height: inherit;
    color: inherit;
    white-space: normal;
}
.decktool progress {
    vertical-align: baseline;
}
.decktool [type="number"]::-webkit-inner-spin-button,
  .decktool [type="number"]::-webkit-outer-spin-button {
    height: auto;
}
.decktool [type="search"] {
    outline-offset: -2px;
    -webkit-appearance: none;
}
.decktool [type="search"]::-webkit-search-cancel-button,
  .decktool [type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}
.decktool ::-webkit-file-upload-button {
    font: inherit;
    -webkit-appearance: button;
}
.decktool output {
    display: inline-block;
}
.decktool summary {
    display: list-item;
    cursor: pointer;
}
.decktool template {
    display: none;
}
.decktool [hidden] {
    display: none !important;
}
