.decktool-header {
  text-align: center;
}
.decktool label {
  margin-bottom: 0;
  margin-right: 0.25rem;
}
.decktool .close {
  margin: 0;
  padding: 0;
  font-size: 1.4em;
  cursor: pointer;
}
.decktool .modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.decktool .modal-title {
  margin-bottom: 0;
}
.decktool .modal-body {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 1200px) {
.decktool .container {
    max-width: 100%;
}
}
.decktool .btn {
  cursor: pointer;
  border: 0;
  line-height: 24px;
}
.decktool .btn-primary:focus {
  background-color: #b97b51;
  color: #fff;
}
.app {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.app-section {
  margin-top: 0.5rem;
  width: 100%;
}
.app-forms {
  order: 1;
}
.app-deck {
  order: 3;
}
@media (min-width: 768px) {
.app-deck {
      width: calc(100% - 454px);
      order: 2;
}
}
.app-builder {
  order: 2;
}
@media (min-width: 768px) {
.app-builder {
      width: 434px;
      margin-left: 20px;
      order: 3;
}
}
.app-builder-intro {
  display: flex;
  margin-bottom: 1rem;
}
.app-builder-intro > div {
    margin-right: 1rem;
}
.app-forms {
  display: flex;
  flex-direction: column;
}
.app-forms > .form-group {
    display: flex;
    flex-direction: column;
}
.app-forms > .form-group input,
    .app-forms > .form-group button,
    .app-forms > .form-group select {
      margin-bottom: 0.5rem;
}
.app-forms input[type="file"] {
    padding: 3px;
}
@media (min-width: 768px) {
.app-forms > .form-group {
      flex-direction: row;
      align-items: center;
}
.app-forms > .form-group input,
      .app-forms > .form-group button,
      .app-forms > .form-group select {
        margin-bottom: 0;
}
.app-forms input[title="Deck Title"] {
      max-width: 300px;
}
.app-forms .btn {
      min-width: 225px;
      max-width: 275px;
      margin-left: 10px;
}
.app-forms .btn.btn-tiny {
        max-width: 40px;
        min-width: 0;
}
.app-forms label {
      min-width: 100px;
      max-width: 150px;
}
}

/**
* Overrides for ygoprodeck.com overrides
*/
.decktool .fas {
  color: #fff;
  /**
        * Weird offset I have no idea why it is there
        */
  position: relative;
  top: 1px;
}
.decktool input[type="text"],
.decktool input[type="email"],
.decktool input[type="password"],
.decktool textarea {
  background: transparent;
  border-radius: 0;
  padding: 0.375rem 0.75rem;
}
.decktool input[type="text"]:focus,
.decktool textarea:focus {
  box-shadow: none;
  padding: 0.375rem 0.75rem;
  margin: 0;
  border-color: #ced4da;
}
.decktool .form-group {
  line-height: 1.5;
}
.deck-card img {
  width: 100px;
  height: 135px;
}
