
.builder-list {
  max-height: 250px;
  width: 100%;
  overflow-x: auto;
  overflow-y: scroll;
  list-style: none;
  padding: 0;
  resize: vertical;
  border: 1px solid #ced4da;
}
@media (min-width: 768px) {
.builder-list {
      max-height: 60vh;
}
}
.builder-list li {
  border-top: 1px solid #ced4da;
}
.builder-list li:first-child {
    border-top: 0;
}
.builder-card {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 10px;
}
.builder-card-name {
  padding: 10px 0;
}
.builder-card-action {
  width: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}
.btn.builder-add {
  width: 34px;
  opacity: 0.8;
  font-size: 1.2em;
  cursor: pointer;
  padding: 0;
  background-color: transparent;
}
.btn.builder-add:active {
    opacity: 1;
}
.btn.builder-add[disabled] {
    opacity: 0.4;
}
.btn.builder-add-main .fas {
    color: #9f7151;
}
.btn.builder-add-extra .fas {
    color: #9683b8;
}
.btn.builder-add-side .fas {
    color: #799a60;
}
.builder-noresults {
  text-align: center;
  padding: 0.5rem 0;
  color: #6c757d;
}
