
.price.price--group {
  margin-bottom: 0.5rem;
}
.price:not(.price--group) {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
.price-mode {
  padding: 5px 8px 2px;
  font-size: 0.9em;
  display: inline-block;
  text-align: center;
}
.price-mode-low {
    background-color: #b8d46c;
}
.price-mode-average {
    background-color: #ffd275;
}
.price-mode-high {
    background-color: #f1a64a;
}
