
@charset "UTF-8";
.form-group-builder {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form-group-builder label {
    padding-right: 0.75rem;
    width: 40%;
}
.form-group-builder label:not(:first-child) {
      margin-left: 1rem;
}

/**
* Multiselect
*/
.multiselect {
  min-height: 38px;
  font-size: inherit;
}
.multiselect__tags {
  min-height: 38px;
  display: block;
  padding: 0.375rem 0.75rem;
  border: 1px solid #ced4da;
  border-radius: 0;
}
.multiselect__tag {
  border-radius: 0;
  color: #fff;
  background: #b97b51;
}
.multiselect__tag-icon:after {
  content: "\D7";
  color: #fff;
}
.multiselect__tag-icon:focus,
.multiselect__tag-icon:hover {
  background: #c89775;
}
.multiselect__current {
  min-height: 38px;
  border-radius: 0;
}
.multiselect__select {
  height: 38px;
  widows: 38px;
}
.multiselect__select:before {
  margin-top: 4px;
  border-style: solid;
  border-width: 5px 5px 0 5px;
  border-color: #999999 transparent transparent transparent;
  content: "";
}
.multiselect__placeholder {
  color: #adadad;
  display: inline-block;
  margin-bottom: 10px;
  padding-top: 2px;
}
.multiselect__element {
  display: block;
}
.multiselect__option {
  min-height: 38px;
  line-height: 16px;
}
.multiselect__option:after {
  position: absolute;
  line-height: 38px;
}
.multiselect__option--highlight {
  background: #b97b51;
  color: white;
}
.multiselect__option--highlight:after {
  background: #b97b51;
  color: white;
}
.multiselect__option--selected {
  background: #f3f3f3;
  color: #35495e;
  font-weight: bold;
}
.multiselect__option--selected.multiselect__option--highlight {
  background: #d26c6c;
}
.multiselect__option--selected.multiselect__option--highlight:after {
  background: #d26c6c;
}
.multiselect__option--group-selected.multiselect__option--highlight {
  background: #d26c6c;
}
.multiselect__option--group-selected.multiselect__option--highlight:after {
  background: #d26c6c;
}
.multiselect__input,
.multiselect__single {
  line-height: inherit;
}
.multiselect__option {
  min-height: 38px;
}
